import { Component, OnInit, AfterViewInit } from '@angular/core';
import * as Chart from 'chart.js';

@Component({
  selector: 'app-summary-result',
  templateUrl: './summary-result.component.html',
  styleUrls: ['./summary-result.component.css']
})
export class SummaryResultComponent implements AfterViewInit {
  canvas: any;
  ctx: any;

  ngAfterViewInit() {
    this.canvas = document.getElementById('pieChart');
    this.ctx = this.canvas.getContext('2d');
    let myChart = new Chart(this.ctx, {
      type: 'doughnut',
      data: {
          labels: ["Pass", "Fail"],
          datasets: [{
              label: 'Summary',
              data: [8, 2],
              backgroundColor: ['black', '#455a64'],
              borderWidth:1,
              borderColor:'#777',
              hoverBorderWidth:2,
              hoverBorderColor:'black'
          }]
      },
      options: {
        responsive: true,
        aspectRatio:1,
        cutoutPercentage:60,
        hover: {
          mode: 'index'
        },
        title:{
            display:true,
            text:'Summary',
            fontSize:25
        },
        tooltips:{
          enabled:true
        }
      
      }
    });
  }
}
